.algolia-places {
  z-index: 9999;
  margin-bottom: 1rem;
}

.map-block-leaflet-list-item {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 0.85rem;
  align-items: center;
  padding: 0.5rem 0;
}

.map-block-leaflet-list-item-action {
  display: flex;
  padding-top: 0.25rem;
}

.map-block-leaflet-list-item-action > *:not(:last-child) {
  margin-right: 0.5rem;
}

.components-resizable-box__container.has-show-handle .components-resizable-box__handle {
  z-index: 600;
}